import React, { Component } from 'react';
import Boy from "./boy.jpg"
import UIkit from "uikit/dist/js/uikit.min.js";
import icon from "uikit/dist/js/uikit-icons.min.js";
import "uikit/dist/css/uikit.min.css";
class Navigation extends Component {
    render() {
        return (
            <nav className="data-uk-navbar-component data-navbar-transparent uk-background-muted uk-dark" data-uk-navbar>

                <div className="uk-navbar-left">
                    {/* eslint-disable-next-line */}
                    <a className="uk-navbar-item uk-logo" href="#.com">Ebenezer Bells Academy</a>
                </div>
                <div className="uk-navbar-right">
                    {/* eslint-disable-next-line */}
                    <a className="uk-navbar-toggle uk-button" data-uk-navbar-toggle-icon href="#" type="button" uk-toggle="target: #offcanvas-navigation"></a>
                </div>

                <div id="offcanvas-navigation" uk-offcanvas="overlay: true; mode:push">
                    <div className="uk-offcanvas-bar">
                        <button className="uk-offcanvas-close" type="button" data-uk-close></button>
                        <img className="uk-border-circle uk-flex-center uk-margin-large-top uk-margin-small-left" width="200" height="200" src={Boy}></img>
                        <ul className="uk-nav uk-nav-default uk-text-medium">
                            {/* {eslint-disable-next-line} */}
                            <li className="active uk-margin-large-top"><a href="#"><span className="uk-light" uk-icon="icon: house"></span> Home</a></li>
                            {/* {eslint-disable-next-line} */}
                            <li className="uk-margin-large-top"><span className="uk-light" data-uk-icon="icon:home"></span> Contact Us</li>
                            {/* {eslint-disable-next-line} */}
                            <li className="uk-margin-large-top"><span className="uk-light" uk-icon="icon: image"></span> Gallery</li>
                            {/* {eslint-disable-next-line} */}
                            <li className="uk-margin-large-top"><span className="uk-light" uk-icon="icon: sign-in"></span> Log in</li>
                        </ul>
                    </div>
                </div>
            </nav>
        );
    }
}

export default Navigation;